import React from 'react'
import { getSiteProps } from 'react-static'

import Paper from './Paper'
import { Intro, Outro } from './content';

class Home extends React.Component {
  state = {opacity: 0}
  
  componentDidMount() {
    if (this.state.opacity === 0) setTimeout(() => this.setState({opacity: 1}), 1)
  }

  
  render() {
    
    return (
    <div className="karla" style={{opacity: this.state.opacity, transition: 'opacity .1s ease-in-out'}}>
      <div className="container grid-md">
        <div className="columns">
          <div className="col-12">
            <h2>Visualizing Philosophy Philosphy</h2>
            <Intro />
          </div>
        </div>
      </div>
      <Paper />
      <div className="container grid-md">
        <div className="columns">
          <div style={{height: 600}} className="col-12">
            <Outro />
          </div>
        </div>
      </div>
    </div>
    )
  }
  
  
  
  
}


export default getSiteProps(() => (<Home/>));



