import {compiler, default as markdown} from 'markdown-to-jsx';

export const compile = mdText => (props={}) => {
  const overrides = Object.assign({}, props.overrides, {
    p: { component: 'div', props: {className: 'p'}}
  })
  
  return compiler(mdText.trim(), { overrides })
}


export const Intro = compile(`
### Introduction
I've lately been thinking a lot about Ideologies and how [Political Philosophy](https://en.wikipedia.org/wiki/Political_philosophy) is thought about and taught. It seems like the conventional thinking about political philosophy is that one should label themselves with the ideas of some long-dead political philosopher. 

Yet I've found you can gain a much deeper understanding of the viewpoints they hold simply by talking and asking them questions regarding their views. In a certain since, you can understand someone's _true_ political viewpoints as the answers to the questions you ask them. I wonder if I were to ask 100 people each 100 multiple choice questions regarding their underlying political philosophy, what would the answers look like? How would that break down compared to how they label themselves?

But of course, what you gain in depth of viewpoints you lose in succinctness. It's hard to fit the nuance of 100 answers into a word or a sentence or a few words.

This quick project has two goals. The first is to experiment with educational mediums for political philosophy inspired by Benadict Evans "explorable explanations" and the brilliant distil.pub journal. And the second is to explore representing some of the ideas of political philosophy and ideologies as broken down into their pieces and visually reassembled. 

Moving off that second one: So how am I approaching taking ideas at the heart of political philosophy and breaking them down into configurable statements? Basically, poorly. For this proof-of-concept, I randomly chose [Rousseau](https://plato.stanford.edu/entries/rousseau/), dove through this ideas, and did my best to interpret and get to the heart of his ideas. 

Rousseau's hypothetical responses are represented in yellow in the visulization below. Drag the the underlined phrases to represent your own ideology in red.

`)

