import React from 'react';

export default (d3Fn) => (
  class D3Container extends React.PureComponent {
    renderD3 = null;
    
    componentDidMount() {
      this.renderD3 = d3Fn(this.refs.mountpoint);
      window.reloadD3 = () => this.renderD3(this.props.data, this.props.options);

      
      this.renderD3(this.props.data, this.props.options);
    }

    componentDidUpdate() {
      this.renderD3(this.props.data, this.props.options);
    }

    render() {
      const { style } = this.props;
      return <div style={style} ref="mountpoint" />
    }
  }
)



