import React from 'react'
import clickDrag from 'react-clickdrag'
import { clamp } from 'ramda'

const lineStyle = (style) => ({
  width: '100%',
  position: 'absolute',
  bottom: 0,
  left: 0,
  borderBottom: '2px solid #d1d3d6',
  overflow: 'hidden',
  whiteSpace: 'nowrap',
  ...style
})


class Underlinable extends React.Component {
  state = {
    I: 0,
    prevI: 0
  }

  componentWillReceiveProps(nextProps) {
    const { dataDrag, options } = nextProps;
    console.log(dataDrag.isMouseDown, this.props.dataDrag.isMouseDown)
    const prevI = nextProps.prevI !== this.props.prevI ? nextProps.prevI : this.state.prevI;
    const I = nextProps.I !== this.props.I ? nextProps.I : this.state.I;

    const within = n => clamp(0, options.length-1)(n);
    
    if (dataDrag.isMouseDown) {
      this.setState( dataDrag.isMoving ? 
        { I: within(prevI + (dataDrag.moveDeltaX/10)) } : 
        { prevI: I }
      );
    }
    
    if (!dataDrag.isMouseDown && this.props.dataDrag.isMouseDown) {
      this.props.handleDrag(this.state)
    }

  }
   
  render() {
    const {options} = this.props;
    const { I } = this.state;
    const roundI = Math.round(I);
    
    const percent = I === 0 ? 0 : 
    I === options.length - 1? 100 :
    (I *100 / options.length-1)
    
    return (
      <div tabIndex="0" className='no-select-hover' style={{display:'inline', position:'relative', cursor: 'col-resize'}}>
        {options[roundI]}
        <div style={lineStyle({})} />
        <div style={lineStyle({
          width: `${percent}%`,
          borderBottom: '2px solid #333'
        })} />
      </div>    
    )
  }
}
const DragUnderlineable = clickDrag(Underlinable, {touch: true});
export default DragUnderlineable;



