import React from 'react'
import { StickyContainer, Sticky } from 'react-sticky'; // https://www.npmjs.com/package/react-sticky
import { Motion, spring }  from 'react-motion'
import * as R from 'ramda'

import Draggable from './Draggable'
import { PaperIntro, compile, options, rosOptions } from './content';

import makeD3Container from '../../components/D3Container';
import PrettyRadar from '../../components/d3-pretty-radar';
const D3Container = makeD3Container(PrettyRadar);


const rosChart= { 
  name: 'Rousseau',
  axes: R.pipe(R.toPairs, R.map(([axis, value]) => ({axis, value:(value+1) * 100})))(rosOptions)
}


export default class Paper extends React.Component {
  state = R.map(I => ({prevI: 0, I:0}))(options)
  
  updateOption = oKey => (nextState) => {
    const newState = (state) => ({
      [oKey]: Object.assign(state[oKey], nextState)
    })
    
    if (nextState) this.setState(newState)
  }

  
  render() {
    console.log('rendered!', this.state)
    const optionComponents = R.mapObjIndexed((val, key) => (
      {component: Draggable, props: {
        key,
        handleDrag: this.updateOption(key),
        options: options[key],
        ...this.state[key]
       }}
    ), options)
    
    const chartData = [ 
      rosChart, 
      {
        name: 'user',
        axes: R.pipe(R.toPairs, R.map(([axis, {I:value}]) => ({axis, value: (value+1) * 100})))(this.state)
      }
    ]
    
    return (
      <StickyContainer className="container grid-lg" style={{marginTop: '2rem'}}>
        <div className="columns col-oneline" >
          <div className="column col-4" style={{width:'36%'}}>
            <Sticky children={({ style }) => (
              <div style={{...style}} >
                <D3Container data={chartData} options={{
                  w: style.width || 340,
                  h: style.width || 340,
                  levels: 5, 
                  roundStrokes: true, 
                  margin: {top: 10, right: 10, bottom: 10, left: 10},
                  maxValue: 400
                }} />  
              </div>
            )} />
          </div>
          <div className="column divider-vert" />
          <div className="column col-7" >
            <PaperIntro overrides={optionComponents}/>
          </div>
        </div>
      </StickyContainer>
    )
  }
  
  
  
  
}
